"use client"

import { useStore } from "@/contexts/store-context"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Package, ArrowRightLeft, Truck, MapPin, FileText, Clock, CheckCircle } from "lucide-react"

export default function StaffDashboardPage() {
  const { transfers, adjustments, products, locations } = useStore()
  
  // Mock task data for warehouse staff
  const pendingTasks = [
    { id: '1', type: 'transfer', priority: 'high', description: 'Move Steel Rods from A1 to B2', dueTime: '2:30 PM' },
    { id: '2', type: 'picking', priority: 'medium', description: 'Pick items for Order #12345', dueTime: '3:00 PM' },
    { id: '3', type: 'receiving', priority: 'low', description: 'Process incoming shipment #67890', dueTime: '4:00 PM' },
    { id: '4', type: 'counting', priority: 'medium', description: 'Count inventory in Zone C', dueTime: '5:00 PM' },
  ]

  const recentTransfers = transfers.slice(-5)
  const locationSummary = locations.map(loc => ({
    ...loc,
    productCount: products.filter(p => Object.keys(p.locationStock || {}).includes(loc.id) && p.locationStock[loc.id] > 0).length
  }))

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high': return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-300'
      case 'medium': return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-300'  
      case 'low': return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300'
      default: return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-300'
    }
  }

  const getTaskIcon = (type: string) => {
    switch (type) {
      case 'transfer': return <ArrowRightLeft className="w-4 h-4" />
      case 'picking': return <Package className="w-4 h-4" />
      case 'receiving': return <Truck className="w-4 h-4" />
      case 'counting': return <FileText className="w-4 h-4" />
      default: return <Package className="w-4 h-4" />
    }
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-foreground">Staff Dashboard</h1>
        <p className="text-muted-foreground mt-2">Your daily warehouse operations and tasks</p>
      </div>

      {/* Today's Summary */}
      <div className="grid md:grid-cols-4 gap-4">
        <Card className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-muted-foreground">Pending Tasks</p>
              <p className="text-2xl font-bold text-foreground">{pendingTasks.length}</p>
            </div>
            <Clock className="w-8 h-8 text-orange-500" />
          </div>
        </Card>
        
        <Card className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-muted-foreground">Active Locations</p>
              <p className="text-2xl font-bold text-foreground">{locations.length}</p>
            </div>
            <MapPin className="w-8 h-8 text-blue-500" />
          </div>
        </Card>
        
        <Card className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-muted-foreground">Transfers Today</p>
              <p className="text-2xl font-bold text-foreground">{recentTransfers.length}</p>
            </div>
            <ArrowRightLeft className="w-8 h-8 text-green-500" />
          </div>
        </Card>
        
        <Card className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-muted-foreground">Completed</p>
              <p className="text-2xl font-bold text-green-600">8</p>
            </div>
            <CheckCircle className="w-8 h-8 text-green-500" />
          </div>
        </Card>
      </div>

      <div className="grid lg:grid-cols-2 gap-6">
        {/* Pending Tasks */}
        <Card className="p-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-foreground">Pending Tasks</h3>
            <Badge variant="outline">{pendingTasks.length} tasks</Badge>
          </div>
          <div className="space-y-3">
            {pendingTasks.map((task) => (
              <div key={task.id} className="flex items-center justify-between p-4 bg-secondary/30 rounded-lg border">
                <div className="flex items-center gap-3">
                  <div className="w-10 h-10 bg-accent rounded-lg flex items-center justify-center">
                    {getTaskIcon(task.type)}
                  </div>
                  <div className="flex-1">
                    <p className="font-medium text-foreground">{task.description}</p>
                    <div className="flex items-center gap-2 mt-1">
                      <Badge variant="secondary" className={getPriorityColor(task.priority)}>
                        {task.priority} priority
                      </Badge>
                      <span className="text-sm text-muted-foreground">Due: {task.dueTime}</span>
                    </div>
                  </div>
                </div>
                <Button size="sm">Start</Button>
              </div>
            ))}
          </div>
        </Card>

        {/* Location Overview */}
        <Card className="p-6">
          <h3 className="text-lg font-semibold text-foreground mb-4">Location Overview</h3>
          <div className="space-y-3">
            {locationSummary.map((location) => (
              <div key={location.id} className="flex items-center justify-between p-3 bg-secondary/30 rounded-lg">
                <div className="flex items-center gap-3">
                  <div className="w-8 h-8 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center">
                    <MapPin className="w-4 h-4" />
                  </div>
                  <div>
                    <p className="font-medium text-foreground">{location.name}</p>
                    <p className="text-sm text-muted-foreground">{location.type}</p>
                  </div>
                </div>
                <div className="text-right">
                  <p className="font-semibold text-foreground">{location.productCount} items</p>
                  <p className="text-xs text-muted-foreground">
                    {Math.round((location.currentUsage / location.capacity) * 100)}% full
                  </p>
                </div>
              </div>
            ))}
          </div>
        </Card>
      </div>

      {/* Recent Transfers */}
      <Card className="p-6">
        <h3 className="text-lg font-semibold text-foreground mb-4">Recent Transfers</h3>
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead>
              <tr className="border-b border-border">
                <th className="text-left py-3 px-4 font-semibold text-foreground">Transfer ID</th>
                <th className="text-left py-3 px-4 font-semibold text-foreground">From</th>
                <th className="text-left py-3 px-4 font-semibold text-foreground">To</th>
                <th className="text-left py-3 px-4 font-semibold text-foreground">Items</th>
                <th className="text-left py-3 px-4 font-semibold text-foreground">Status</th>
                <th className="text-left py-3 px-4 font-semibold text-foreground">Date</th>
              </tr>
            </thead>
            <tbody>
              {recentTransfers.length > 0 ? recentTransfers.map((transfer) => (
                <tr key={transfer.id} className="border-b border-border hover:bg-secondary/50 transition-colors">
                  <td className="py-3 px-4 font-mono text-xs">{transfer.id.slice(0, 8)}</td>
                  <td className="py-3 px-4">{transfer.fromLocation}</td>
                  <td className="py-3 px-4">{transfer.toLocation}</td>
                  <td className="py-3 px-4">{transfer.totalItems}</td>
                  <td className="py-3 px-4">
                    <Badge variant="secondary" className={
                      transfer.status === 'Done' ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300' :
                      'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-300'
                    }>
                      {transfer.status}
                    </Badge>
                  </td>
                  <td className="py-3 px-4 text-muted-foreground">{transfer.date}</td>
                </tr>
              )) : (
                <tr>
                  <td colSpan={6} className="py-8 text-center text-muted-foreground">
                    No recent transfers
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </Card>

      {/* Quick Actions */}
      <Card className="p-6">
        <h3 className="text-lg font-semibold text-foreground mb-4">Quick Actions</h3>
        <div className="grid md:grid-cols-4 gap-4">
          <Button className="h-16 flex-col gap-2" variant="outline">
            <ArrowRightLeft className="w-6 h-6" />
            <span>New Transfer</span>
          </Button>
          <Button className="h-16 flex-col gap-2" variant="outline">
            <Package className="w-6 h-6" />
            <span>Pick Items</span>
          </Button>
          <Button className="h-16 flex-col gap-2" variant="outline">
            <Truck className="w-6 h-6" />
            <span>Receive Stock</span>
          </Button>
          <Button className="h-16 flex-col gap-2" variant="outline">
            <FileText className="w-6 h-6" />
            <span>Count Inventory</span>
          </Button>
        </div>
      </Card>
    </div>
  )
}
